"use client"

import { motion, useInView, Variants, Easing } from "framer-motion"
import { useRef } from "react"

interface Stat {
  value: string
  label: string
}

interface StatsSectionProps {
  stats: Stat[]
  className?: string
}

const easing: Easing = [0.6, -0.05, 0.01, 0.99]

const fadeInUp: Variants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.5,
      ease: easing
    }
  }
}

const container: Variants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.2
    }
  }
}

export function StatsSection({ stats, className }: StatsSectionProps) {
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true })

  return (
    <motion.div
      ref={ref}
      initial="hidden"
      animate={isInView ? "visible" : "hidden"}
      variants={container}
      className={className}
    >
      <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
        {stats.map((stat, index) => (
          <motion.div
            key={index}
            variants={fadeInUp}
            className="relative group"
          >
            <div className="absolute inset-0 bg-orange-500/5 transform rotate-3 rounded-lg group-hover:rotate-0 transition-transform duration-300" />
            <div className="relative p-6 bg-white rounded-lg shadow-sm group-hover:shadow-md transition-shadow duration-300">
              <motion.div
                initial={{ scale: 0.5, opacity: 0 }}
                animate={isInView ? { scale: 1, opacity: 1 } : { scale: 0.5, opacity: 0 }}
                transition={{ 
                  duration: 0.5, 
                  delay: index * 0.1,
                  ease: easing
                }}
                className="text-4xl font-bold text-orange-500 mb-2"
              >
                {stat.value}
              </motion.div>
              <div className="text-gray-600 group-hover:text-gray-900 transition-colors duration-300">
                {stat.label}
              </div>
            </div>
          </motion.div>
        ))}
      </div>
    </motion.div>
  )
}
