"use client"

import { cn } from "@/lib/utils"
import Image from "next/image"
import { motion, Variants } from "framer-motion"

interface EnhancedHeroProps {
  title: string
  description?: string
  image: string
  className?: string
  children?: React.ReactNode
  overlay?: boolean
}

const fadeIn: Variants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.6,
      ease: [0.6, -0.05, 0.01, 0.99]
    }
  }
}

const stagger: Variants = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.2
    }
  }
}

export function EnhancedHero({
  title,
  description,
  image,
  className,
  children,
  overlay = true,
}: EnhancedHeroProps) {
  return (
    <div className={cn(
      "relative min-h-[600px] flex items-center justify-center overflow-hidden",
      className
    )}>
      <div className="absolute inset-0">
        <Image
          src={image}
          alt="Background"
          fill
          sizes="100vw"
          priority
          className="object-cover"
        />
        {overlay && (
          <div className="absolute inset-0 bg-gradient-to-r from-black/70 via-black/50 to-black/70" />
        )}
      </div>

      <motion.div
        initial="hidden"
        animate="visible"
        variants={stagger}
        className="relative z-10 container mx-auto px-4 py-20 text-center"
      >
        <motion.h1
          variants={fadeIn}
          className="text-4xl md:text-6xl font-bold text-white mb-6 leading-tight"
        >
          {title}
        </motion.h1>
        
        {description && (
          <motion.p
            variants={fadeIn}
            className="text-xl md:text-2xl text-white/90 mb-8 max-w-3xl mx-auto"
          >
            {description}
          </motion.p>
        )}

        <motion.div variants={fadeIn}>
          {children}
        </motion.div>

        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 1, duration: 1 }}
          className="absolute bottom-0 left-0 right-0 h-32 bg-gradient-to-t from-black/30 to-transparent"
        />
      </motion.div>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: [0, 0.5, 0] }}
        transition={{
          duration: 3,
          repeat: Infinity,
          repeatType: "reverse"
        }}
        className="absolute inset-0 bg-gradient-to-t from-black/10 via-orange-500/10 to-transparent"
      />
    </div>
  )
}
